import React, { Component } from "react";
import { db } from "../../libs/db";

import ContentLoader from "../layout/ContentLoader";
import ReactMarkdown from "react-markdown";
import gfm from "remark-gfm";
import CardEdit, { CardEditControl } from "./Card-Edit";
import { pathDestructure } from "../../libs/utils";
import { InfoSquare as IconInfoSquare } from "react-bootstrap-icons";

//

export default class Card extends Component {
	constructor(props) {
		super(props);
		this.state = {
			card: null,
			currentLang: props.lang || "",
			content: "",
			isReading: false,
			isEdit: false,
		};

		this.fetchCardData = this.fetchCardData.bind(this);
	}

	async componentDidMount() {
		// Anonymously log in and display comments on load
		try {
			this.fetchCardData();
		} catch (error) {
			console.error(error);
		}
	}

	documentNotFound(id) {
		console.log(`Card document '${id}' not found.`);
		this.setState({
			card: {
				_id: null,
				name: "",
				galery: null,
				body: {},
				lang: [],
			},
			content: "",
			currentLang: "",
		});
	}

	getAvailableLangs() {
		if (this.state.card) {
			return this.state.card.lang.map((lang) => <span> {lang} </span>);
		} else return null;
	}
	getContentByLang() {
		// debugger;
		const lang = this.state.currentLang;
		if (this.state.card) {
			const langs = this.state.card.lang;
			// 1: check for languages count
			if (langs.length > 0) {
				// 2: check for exist currentLang in card
				if (langs.includes(lang)) {
					return this.state.card.body[lang];
				} else {
					this.setState({ langChange: true, currentLang: langs[0] });
				}
			} else {
				return <h1>No content.</h1>;
			}
		} else return "";
	}

	fetchCardData() {
		this.setState({ isReading: true });
		// query the remote DB and update the component state
		if (this.props.id) {
			db.collection("card")
				.find({ _id: this.props.id }, { limit: 1 })
				.asArray()
				.then((results) => {
					if (results && results.length !== 0) {
						this.setState({
							card: results[0],
						});
					} else {
						this.documentNotFound(this.props.id);
					}
					this.setState({ isReading: false });
				});
		} else if (this.props.name) {
			const { path, name } = pathDestructure(this.props.name);
			db.collection("card")
				.find({ path, name }, { limit: 1 })
				.asArray()
				.then((results) => {
					if (results && results.length !== 0) {
						this.setState({
							card: results[0],
						});
					} else {
						this.documentNotFound(this.props.name);
					}
					this.setState({ isReading: false });
				});
		}
	}

	updateContent = (cardData) => {
		this.setState({ card: cardData });
	};

	showEdit = () => {
		this.setState({ isEdit: true });
	};

	hideEdit = () => {
		this.setState({ isEdit: false });
	};

	render() {
		return (
			<React.Fragment>
				<div id="main-content">
					<ContentLoader busy={this.state.isReading}>
						<div className="card-control">
							<CardEditControl onEdit={this.showEdit} />
						</div>
						{this.state.langChange && (
							<div style={{ color: "#F44" }}>
								<IconInfoSquare
									size="32"
									style={{ float: "left", marginRight: "10px" }}
								/>
								<p>
									This article is not available in the language of your choice.
									<br />
									Language versions available:
									{this.getAvailableLangs()}
								</p>
							</div>
						)}
						<ReactMarkdown
							plugins={[
								[gfm, { tableCellPadding: true, tablePipeAlign: true }],
							]}
							className="markdown"
							children={this.getContentByLang()}
						/>
						{this.state.isEdit && (
							<CardEdit
								card={this.state.card}
								onUpdate={this.updateContent}
								onClose={this.hideEdit}
							/>
						)}
					</ContentLoader>
				</div>
			</React.Fragment>
		);
	}
}
